body {
    font-family: 'Open Sans','Montserrat', sans-serif; 
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
button {
  cursor: pointer;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}